body {
  background: #f3f4f6;
  font-family: 'Segoe UI', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.calculator {
  background: #fff;
  border-radius: 20px;
  padding: 20px;
  width: 260px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

.display {
  background: #000;
  color: #fff;
  font-size: 2.5rem;
  text-align: right;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 15px;
  overflow-x: auto;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}

button {
  font-size: 1.2rem;
  padding: 15px;
  border: none;
  border-radius: 10px;
  background: #f9fafb;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

button:hover {
  transform: scale(1.05);
}

button.red {
  background: #f87171;
  color: white;
}

button.yellow {
  background: #facc15;
  color: white;
}

button.green {
  background: #22c55e;
  color: white;
}
